import { Component, OnInit } from '@angular/core';
import {MatDialog} from '@angular/material/dialog';
import { DeleteDialogBoxComponent } from '../delete-dialog-box/delete-dialog-box.component';
import { DataService } from '../Services/data.service';
import { Contact } from '../Model/Contact';
import { ActivatedRoute, Router } from '@angular/router';


@Component({
  selector: 'app-contact-detail',
  templateUrl: './contact-detail.component.html',
  styleUrls: ['./contact-detail.component.css']
})
export class ContactDetailComponent implements OnInit { 

  public contact : Contact;
  public id : any;

  constructor(public dialog : MatDialog, private contactService: DataService,private activeRoute: ActivatedRoute) { 
    console.log("contact-detail-component");
  }

  ngOnInit() {
    this.activeRoute.params.subscribe(param => (this.contact = this.contactService.findContactById(param['id']),this.id=param['id']));
  }

  openDialog(): void {
    const dialogRef = this.dialog.open(DeleteDialogBoxComponent,{
      data:{id:this.id}
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log("Dialog closed");

    });
  }

}
