import { Component, OnInit, EventEmitter } from '@angular/core';
import { DataService } from '../Services/data.service';
import { Contact } from '../Model/Contact';
import { ActivatedRoute, Router } from '@angular/router';
import {FormGroup,FormControl,FormBuilder, Validators} from '@angular/forms';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-add-contact',
  templateUrl: './add-contact.component.html',
  styleUrls: ['./add-contact.component.css']
})
export class AddContactComponent implements OnInit {

  public contact : Contact;
  public contactForm : FormGroup;
  contactListUpdated= new EventEmitter();
  

  //Validations
  validation_messages = {
    'name':[
      {type:'required',message:"name is required"}
    ],
    'email':[
      {type: 'required', message: "email is required"},
      {type:'pattern',message:"email doesn't match"}
    ],
    'mobile':[
      {type:'required',message:"Mobile number is required"},
      {type:'pattern',message:"Invalid mobile number"}
    ],
    'phone':[
      {type:'required',message:"Phone number is required"},
      {type:'pattern',message:"Invalid phone number"}
    ],
    'website':[
      {type:'required',message:"Website is required"},
      {type:'pattern',message:"Invalid website"}
    ],
    'address':[
      {type:'required',message:"address is required"}
    ]
  }


  constructor(private dataService : DataService,private router:ActivatedRoute,private builder:FormBuilder,private route:Router) {
    this.contact = new Contact({});
    console.log("add-contact-component");
   }

  ngOnInit() {
    this.initialiseForm();
    this.router.params.subscribe(param => {
      if(param['id']){
        this.contact = this.dataService.findContactById(param['id']);
        this.initialiseForm();
      }
    });
  }
  
  initialiseForm(){
    this.contactForm = this.builder.group({
      id : new FormControl(this.contact.id),
      name: new FormControl(this.contact.name, Validators.required),
      email: new FormControl(this.contact.email, [Validators.required,Validators.pattern('[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$')]),
      mobile: new FormControl(this.contact.mobile, [Validators.required,Validators.minLength(10),Validators.maxLength(10),Validators.pattern('[0-9]{10}')]),
      phone: new FormControl(this.contact.phone, [Validators.required,Validators.minLength(10),Validators.maxLength(10),Validators.pattern('[0-9]{10}')]),
      website: new FormControl(this.contact.website, [Validators.required,Validators.pattern('https?://.+')]),
      address: new FormControl(this.contact.address, Validators.required),
    });
  }

  AddContact():void{
    console.log(this.contactForm.valid);
    if(this.contactForm.valid){
      this.dataService.addContact(this.contactForm.value);
      this.contactListUpdated.emit({isUpdated: true, contact: this.contactForm.value});
      this.contactListUpdated.emit({isAdded: true, contact: this.contactForm.value});
      this.contactListUpdated.emit({isDeleted: true, contact: this.contactForm.value});
      //this.route.navigateByUrl('/');
    }else{
      console.log("Invalid form");
    }
    
  }

}
